.home {
  font-family: sans-serif;
  color: #333;
  background-color: #f9f9f9;
  line-height: 1.6;
  padding: 40px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.hero p {
  font-size: 1.25rem;
  margin-bottom: 1.5rem;
  color: #555;
}

.cta {
  display: inline-block;
  padding: 10px 20px;
  background: #007bff;
  color: white;
  border-radius: 4px;
  text-decoration: none;
  font-weight: bold;
}

.cta:hover {
  background: #0056b3;
}